import React from 'react';
import './VideoItem.css';

const VideoItem = ({ id, videoInfo, onVideoSelect }) => {
	return (
		<div
			className="video-item item"
			onClick={() => onVideoSelect(id, videoInfo)}
		>
			<img
				className="ui image"
				src={videoInfo.thumbnails.medium.url}
				alt={videoInfo.title}
			/>
			<div className="content">
				<div className="header">{videoInfo.title}</div>
			</div>
		</div>
	);
};

export default VideoItem;
