import React from 'react';
import SearchBar from './SearchBar.js';
import VideoList from './VideoList.js';
import VideoDetail from './VideoDetail.js';
import youtube from '../api/youtube.js';

class App extends React.Component {
	state = { videos: [], selectedVideo: { id: null, videoInfo: {} } };

	componentDidMount() {
		this.onTermSubmit('youtube api');
	}

	onTermSubmit = async (term) => {
		const res = await youtube.get('/search', {
			params: {
				q: term
			}
		});

		const { id, snippet } = res.data.items[0];

		this.setState(() => ({
			videos: res.data.items,
			selectedVideo: { id: id, videoInfo: snippet }
		}));
	};

	onVideoSelect = (id, videoInfo) => {
		this.setState(() => ({
			selectedVideo: { id: id, videoInfo: videoInfo }
		}));
	};

	render() {
		return (
			<div className="ui container">
				<SearchBar onTermSubmit={this.onTermSubmit}></SearchBar>
				<div className="ui grid">
					<div className="ui row">
						<div className="eleven wide column">
							<VideoDetail
								id={this.state.selectedVideo.id}
								videoInfo={this.state.selectedVideo.videoInfo}
							/>
						</div>
						<div className="five wide column">
							<VideoList
								videos={this.state.videos}
								onVideoSelect={this.onVideoSelect}
							/>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default App;
